@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&family=Ubuntu:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{top:0;position:-webkit-sticky;position:sticky;display:flex;align-items:center;justify-content:space-between;padding:20px;background-color:#4d52ff;box-shadow:0px 0px 20px 0px rgba(0,0,0,0.2)}header .header__nav__link{color:#fff;padding:20px}header .header__nav__button{color:#fff;border:2px solid white;margin-left:20px;padding:7px 34px;background-color:#4d52ff;border-radius:20px}header .header__nav__button:hover{color:#4d52ff;background-color:#fff}

.hero{background:#2c385d;padding-top:35vh;height:65vh}.hero__greetings,.hero__role{color:#4d52ff}.hero__name,.hero__role,.hero__intro{margin:0}.hero__intro{margin-top:20px}

.about{display:grid;padding:35vh 0;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-gap:30px;gap:30px;grid-template-areas:"about__info about__info about__picture about__links"}.about__info{grid-area:about__info}.about__info__title{background:#0a1334;margin-top:-20px;padding-right:20px;margin-bottom:0;display:inline-block}.about__info__line{color:#4d52ff;margin:0}.about__picture img{max-width:90%;margin:5%}.about__links{text-align:center;grid-area:about__links;border:1px solid #4d52ff;border-radius:20px;background:#0a1334}.about__links__more{margin-bottom:50px}

.skills{background:#4d52ff;height:100vh}

body{font-family:"Open Sans",sans-serif;font-weight:normal;color:#fff}h1,h2,h3,h4,h5,h6{font-family:"Ubuntu",sans-serif}h1{font-size:64px}h2{font-size:48px}h3{font-size:36px}h4{font-size:28px}span{font-size:18px}p{font-size:18px}a{font-size:18px}.large-text{font-size:24px}@-webkit-keyframes move-it{0%{background-position:0 0;background-position:initial}100%{background-position:100px 0px}}@keyframes move-it{0%{background-position:0 0;background-position:initial}100%{background-position:100px 0px}}@-webkit-keyframes hue-mutate{0%,100%{-webkit-filter:hue-rotate(-50deg);filter:hue-rotate(-50deg)}50%{-webkit-filter:hue-rotate(50deg);filter:hue-rotate(50deg)}}@keyframes hue-mutate{0%,100%{-webkit-filter:hue-rotate(-50deg);filter:hue-rotate(-50deg)}50%{-webkit-filter:hue-rotate(50deg);filter:hue-rotate(50deg)}}@-webkit-keyframes bright-mutate{0%,100%{-webkit-filter:brightness(1);filter:brightness(1)}50%{-webkit-filter:brightness(3);filter:brightness(3)}}@keyframes bright-mutate{0%,100%{-webkit-filter:brightness(1);filter:brightness(1)}50%{-webkit-filter:brightness(3);filter:brightness(3)}}@keyframes move-it{0%{background-position:0 0;background-position:initial}100%{background-position:100px 0px}}html{background:#0a1334}.max-content{max-width:1140px;margin:auto}a{text-decoration:none}.button{cursor:pointer;border-radius:20px;font-size:18px;padding:7px 34px;margin:10px;border:2px solid #fff;background:#4d52ff}.button__primary{border-color:#4d52ff;color:#fff}.button__primary__void{border-color:#4d52ff;background:#0a1334;color:#4d52ff}

